*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: white;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 15px;
}

.common-input {
  margin: 10px 0 20px 0;
  width: 250px;
  padding: 10px 15px;
  font-size: 16px;

  outline: none;
  border-radius: 50px;

}

.common-btn {
  margin-top: 30px;
  align-self: center;
  padding: 8px 70px;

  font-size: 18px;

  background: gray;
  border: 2px solid white;
  border-radius: 50px;
  color: white;

  transition: all 0.2s linear;
  cursor: pointer;
}

.common-btn:hover {
  background: white;
  color: gray;
}

.error-p {
  color: #ea3636;
}